import React, {Component} from 'react'
import {actions} from "mirrorx";
import {FormControl, Select} from "tinper-bee";
import FormList from 'components/FormList';
import SearchPanel from 'components/SearchPanel';

import './index.less'

const FormItem = FormList.Item;
const {Option} = Select;

class SearchArea extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    /** 
     * @param {*} error 
     * @param {Object} values 
     */
    search = () => {
        this.props.form.validateFields((err, values) => {
            //  
            if(!err){
                let {pageSize} = this.props.orderObj;
                values.pageIndex = 0;
                values.pageSize = pageSize;
                actions.masterDetailOne.loadList(values);
            }
        });
    }

    reset = () => {
        this.props.form.resetFields();
    }

    render() {
        let {form: {getFieldProps}} = this.props;
        return (
            <SearchPanel
                reset={this.reset}
                search={this.search}
            >

                <FormList size="sm">
                    <FormItem
                        label=""
                    >
                        <FormControl
                            placeholder=''
                            {...getFieldProps('search_orderCode', {initialValue: '',})}
                        />
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <FormControl placeholder='' {...getFieldProps('search_orderName', {initialValue: '',})}/>
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <Select {...getFieldProps('search_orderType', {initialValue: ''})}>
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                            <Option value="3"></Option>
                        </Select>
                    </FormItem>

                    <FormItem
                        label=""
                    >
                        <Select {...getFieldProps('search_bpmState', {initialValue: ''})}>
                            <Option value=""></Option>
                            <Option value={0}></Option>
                            <Option value={1}></Option>
                            <Option value={2}></Option>
                            <Option value={3}></Option>
                        </Select>
                    </FormItem>
                </FormList>

            </SearchPanel>
        )
    }
}

export default FormList.createForm()(SearchArea)
